

// @mui material components
import Card from "@mui/material/Card";

// Soft UI Dashboard React components
import SoftBox from "components/SoftBox";
import SoftTypography from "components/SoftTypography";
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
// Soft UI Dashboard React examples
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import Footer from "examples/Footer";
import Table from "examples/Tables/Table";

// Data
import authorsTableData from "layouts/tables/data/authorsTableData";
import projectsTableData from "layouts/tables/data/projectsTableData";

function Tables() {
  const [subjects, setSubjects] = useState([]);
  const [classes, setClasses] = useState([]);
  const [createClassId, setCreateClassId] = useState('');
  const [displayClassId, setDisplayClassId] = useState('');
  const [selectedSubjects, setSelectedSubjects] = useState([]);
  const [message, setMessage] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [selectedSubject, setSelectedSubject] = useState(null);

  useEffect(() => {
    fetchData();
  }, [displayClassId]);

  const fetchData = async () => {
    try {
      if (displayClassId) {
        const subjectsResponse = await axios.get('http://localhost:5000/api/subjects', {
          params: { classId: displayClassId }
        });
        const fetchedSubjects = subjectsResponse.data;

        if (fetchedSubjects.length > 0) {
          const subjectsInfo = fetchedSubjects.subjects;
          setSubjects(subjectsInfo);
        } else {
          setSubjects([]);
          console.log('No subjects available for the selected class.');
        }
      }
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  };

  useEffect(() => {
    const fetchClasses = async () => {
      try {
        const response = await axios.get('http://localhost:5000/api/classes');
        setClasses(response.data); // Update classes state with fetched class data
      } catch (error) {
        console.error('Error fetching classes:', error);
      }
    };

    fetchClasses();
  }, []);

  const handleCreate = async (e) => {
    e.preventDefault();
    try {
      // Map selectedSubjects array to include subjectId and status
      const subjectsToSend = selectedSubjects.map(subjectName => ({
        subjectName,
        subjectId: '', // You may need to generate subjectId here
        status: 'ACTIVE' // Set default status to ACTIVE
      }));

      const response = await axios.post('http://localhost:5000/api/subjects', { subjects: subjectsToSend, classId: createClassId });
      setMessage(response.data.message);
      fetchData(); // Refresh subjects after creation
      setSelectedSubjects([]); // Clear selected subjects after creation
      setCreateClassId('');
    } catch (error) {
      setMessage(error.response.data.message);
    }
  };

  const handleDelete = async (id) => {
    try {
      await axios.delete(`http://localhost:5000/api/subjects/${id}`);
      setMessage('Subject deleted successfully');
      fetchData(); // Refresh subjects after deletion
    } catch (error) {
      setMessage(error.response.data.message);
    }
  };

  const handleUpdateModal = (subject) => {
    setSelectedSubject(subject);
    setShowModal(true);
  };

  const handleUpdate = async () => {
    try {
      const response = await axios.put(`http://localhost:5000/api/subjects/${selectedSubject._id}`, {
        subjects: selectedSubject.subjects,
        classId: selectedSubject.classId
      });
      setMessage(response.data.message);
      setShowModal(false);
      fetchData(); // Refresh subjects after update
    } catch (error) {
      setMessage(error.response.data.message);
    }
  };

  const handleCheckboxChange = (subjectName) => {
    setSelectedSubjects(prevState => {
      if (prevState.includes(subjectName)) {
        return prevState.filter(name => name !== subjectName);
      } else {
        return [...prevState, subjectName];
      }
    });
  };

  return (
    <DashboardLayout>
      <DashboardNavbar />
      <div className="container">
      <h1>Subjects</h1>
      <form onSubmit={handleCreate}>
        <div className="mb-3">
          <div>
            <h3>Select Subjects:</h3>
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                value="Telugu"
                checked={selectedSubjects.includes("Telugu")}
                onChange={() => handleCheckboxChange("Telugu")}
              />
              <label htmlFor='telugu' className="form-check-label">Telugu</label>
            </div>
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                value="Hindi"
                checked={selectedSubjects.includes("Hindi")}
                onChange={() => handleCheckboxChange("Hindi")}
              />
              <label htmlFor='hindi' className="form-check-label">Hindi</label>
            </div>
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                value="English"
                checked={selectedSubjects.includes("English")}
                onChange={() => handleCheckboxChange("English")}
              />
              <label htmlFor='english' className="form-check-label">English</label>
            </div>
            <div className="form-check">
              <input
                className="form-check-input"
                type="checkbox"
                value="Maths"
                checked={selectedSubjects.includes("Maths")}
                onChange={() => handleCheckboxChange("Maths")}
              />
              <label htmlFor='maths' className="form-check-label">Maths</label>
            </div>
          </div>
        </div>
        <div className="mb-3">
          <select value={createClassId} onChange={(e) => setCreateClassId(e.target.value)} className="form-select" required>
            <option value="">Select Class to Create Subject</option>
            {classes.map((classItem) => (
              <option key={classItem._id} value={classItem._id}>{classItem.className}</option>
            ))}
          </select>
        </div>
        <button type="submit" className="btn btn-primary">Create Subject</button>
      </form>
      <div className="mb-3">
        <h2>Select Class to Display Subjects:</h2>
        <select value={displayClassId} onChange={(e) => setDisplayClassId(e.target.value)} className="form-select" required>
          <option value="">Select Class to Display Subjects</option>
          {classes.map((classItem) => (
            <option key={classItem._id} value={classItem._id}>{classItem.className}</option>
          ))}
        </select>
      </div>
      <table className="table">
  <thead>
    <tr>
      <th>Name</th>
      <th>Class</th>
      <th>Actions</th>
    </tr>
  </thead>
  <tbody>
    {subjects.map((subject) => (
      <tr key={subject._id}>
        <td>{subject.subjectName}</td>
        <td>{subject.classId.className}</td>
        <td>
          <button className="btn btn-primary" onClick={() => handleUpdateModal(subject)}>Update</button>
          <button className="btn btn-danger" onClick={() => handleDelete(subject._id)}>Delete</button>
        </td>
      </tr>
    ))}
  </tbody>
</table>

      {message && <p>{message}</p>}
      {showModal && (
        <div className="modal" tabIndex="-1" role="dialog" style={{ display: 'block' }}>
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Update Subject</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={() => setShowModal(false)}>
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <input type="text" value={selectedSubject?.subjectName} onChange={(e) => setSelectedSubject({ ...selectedSubject, subjectName: e.target.value })} className="form-control" placeholder="Subject Name" />
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-primary" onClick={handleUpdate}>Update</button>
                <button type="button" className="btn btn-secondary" onClick={() => setShowModal(false)}>Close</button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
      <Footer />
    </DashboardLayout>
  );
}

export default Tables;
